@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100;500;600;700;800;900&display=swap");
.container {
    font-family: "Montserrat", sans-serif;
}
.body {
    position: absolute;
    top: 4rem;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    flex-direction: row;
}
.content {
    position: relative;
    top: 0;
    bottom: 0;
    width: 100%;
    overflow-y: auto;
}
@media (max-width: 480px) {
    html {
        font-size: 14px;
    }
}
@media (min-width: 768px) {
    html {
        font-size: 14px;
    }
}

@media (min-width: 1024px) {
    html {
        font-size: 16px;
    }
}

@media (min-width: 1440px) {
    html {
        font-size: 20px;
    }
}
